import React from 'react';
import Container from '../../common/Container';
import Header from '../../common/Header';
import Section from '../../common/Section';

export default () => (
    <Container>
        <Header title="O autorze" />
        <Section
            title="ukasz Susowicz"
            body={
                <>
                    <p>
                        Cze! Nazywam si ukasz Susowicz i jestem <strong>twrc tej aplikacji</strong>.
                    </p>
                    <p>
                        Jestem pasjonatem programowania i od kilku lat zajmuj si tworzeniem aplikacji webowych. Moim celem jest dostarczanie uytkownikom narzdzi, ktre uatwiaj codzienne ycie i zwikszaj produktywno.
                        W wolnym czasie lubi czyta ksiki o nowych technologiach, eksperymentowa z nowymi frameworkami oraz uczestniczy w konferencjach branowych.
                        Jeli masz pytania lub chcesz si ze mn skontaktowa, zapraszam do odwiedzenia mojej strony internetowej lub profili w mediach spoecznociowych.
                    </p>
                </>
            }
        />
    </Container>
);